'use client'

import { useEffect, useMemo, useState } from 'react'
import { useRouter } from 'next/navigation'
import { useAuth } from '@/context/AuthContext'
import {
  fetchSalons,
  fetchGlobalStaff,
  fetchGlobalServices,
  listStaffAssignmentsForSalon,
  listServiceAssignmentsForSalon,
  assignStaffToSalon,
  unassignStaffFromSalon,
  assignServiceToSalon,
  unassignServiceFromSalon,
  type Salon,
  type GlobalStaff,
  type GlobalService
} from '@/services/api'

import {
  Container, Tabs, Tab, Box, Stack, Typography, TextField, MenuItem, Paper, Divider,
  List, ListItem, ListItemText, IconButton, Button, Chip, Tooltip, Dialog, DialogTitle,
  DialogContent, DialogActions
} from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import DeleteIcon from '@mui/icons-material/Delete'
import TuneIcon from '@mui/icons-material/Tune'
import AdminBreadcrumbs from '@/components/AdminBreadcrumbs'

function fuzzyIncludes(hay: string, needle: string) {
  return hay.toLowerCase().includes(needle.toLowerCase())
}

export default function AdminAssignmentsPage() {
  const { user } = useAuth()
  const router = useRouter()

  const [tab, setTab] = useState<'staff' | 'services'>('staff')
  const [salons, setSalons] = useState<Salon[]>([])
  const [salonId, setSalonId] = useState<string>('')

  // global catalogs
  const [globalStaff, setGlobalStaff] = useState<GlobalStaff[]>([])
  const [globalServices, setGlobalServices] = useState<GlobalService[]>([])

  // assigned lists (for current salon)
  const [assignedStaff, setAssignedStaff] = useState<GlobalStaff[]>([])
  const [assignedServices, setAssignedServices] = useState<(GlobalService & { price: number; duration: number })[]>([])

  // search
  const [queryLeft, setQueryLeft] = useState('')
  const [queryRight, setQueryRight] = useState('')

  // override dialog (services)
  const [overrideOpen, setOverrideOpen] = useState(false)
  const [overrideSvc, setOverrideSvc] = useState<GlobalService | null>(null)
  const [overridePrice, setOverridePrice] = useState<number | ''>('')
  const [overrideDuration, setOverrideDuration] = useState<number | ''>('')

  useEffect(() => {
    if (!user) { router.replace('/login'); return }
    if (user.role !== 'admin') { router.replace('/'); return }
  }, [user])

  useEffect(() => {
    (async () => {
      const s = await fetchSalons()
      setSalons(s)
      if (s[0]?._id) setSalonId(s[0]._id)
      // load global registries
      const [staff, services] = await Promise.all([fetchGlobalStaff(), fetchGlobalServices()])
      setGlobalStaff(staff)
      setGlobalServices(services)
    })().catch(() => {})
  }, [])

  // load assignments when salonId or tab changes
  useEffect(() => {
    (async () => {
      if (!salonId) return
      if (tab === 'staff') {
        const list = await listStaffAssignmentsForSalon(salonId)
        setAssignedStaff(list)
      } else {
        const list = await listServiceAssignmentsForSalon(salonId)
        setAssignedServices(list)
      }
    })().catch(() => {})
  }, [salonId, tab])

  const leftList = useMemo(() => {
    const base = tab === 'staff' ? globalStaff : globalServices
    if (!queryLeft.trim()) return base
    return base.filter(item => {
      const text = tab === 'staff'
        ? `${(item as GlobalStaff).name || ''} ${(item as GlobalStaff).email || ''}`
        : `${(item as GlobalService).title} ${(item as GlobalService).description || ''}`
      return fuzzyIncludes(text, queryLeft)
    })
  }, [tab, globalStaff, globalServices, queryLeft])

  const rightList = useMemo(() => {
    const base = tab === 'staff' ? assignedStaff : assignedServices
    if (!queryRight.trim()) return base
    return base.filter(item => {
      const text = tab === 'staff'
        ? `${(item as GlobalStaff).name || ''} ${(item as GlobalStaff).email || ''}`
        : `${(item as GlobalService).title} ${(item as any).price}`
      return fuzzyIncludes(text, queryRight)
    })
  }, [tab, assignedStaff, assignedServices, queryRight])

  const assign = async (id: string) => {
    if (!salonId) return
    if (tab === 'staff') {
      await assignStaffToSalon(id, salonId)
      setAssignedStaff(await listStaffAssignmentsForSalon(salonId))
    } else {
      // open override dialog for services
      const svc = globalServices.find(s => s._id === id) || null
      setOverrideSvc(svc)
      setOverridePrice('')
      setOverrideDuration('')
      setOverrideOpen(true)
    }
  }

  const applyServiceOverride = async () => {
    if (!overrideSvc || !salonId) return
    await assignServiceToSalon({
      serviceId: overrideSvc._id,
      salonId,
      priceOverride: overridePrice === '' ? null : Number(overridePrice),
      durationOverride: overrideDuration === '' ? null : Number(overrideDuration),
    })
    setAssignedServices(await listServiceAssignmentsForSalon(salonId))
    setOverrideOpen(false)
  }

  const unassign = async (id: string) => {
    if (!salonId) return
    if (tab === 'staff') {
      await unassignStaffFromSalon(id, salonId)
      setAssignedStaff(await listStaffAssignmentsForSalon(salonId))
    } else {
      await unassignServiceFromSalon(id, salonId)
      setAssignedServices(await listServiceAssignmentsForSalon(salonId))
    }
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <AdminBreadcrumbs items={[{ label: 'Mein Salon', href: '/admin' }, { label: 'Zuordnungen' }]} />

      <Stack direction="row" spacing={2} alignItems="center" sx={{ mb: 2 }}>
        <Typography variant="h4" fontWeight={800} sx={{ flex: 1 }}>Zuordnungen</Typography>
        <TextField
          select size="small" label="Salon"
          value={salonId} onChange={(e)=> setSalonId(e.target.value)}
          sx={{ minWidth: 220 }}
        >
          {salons.map(s => <MenuItem key={s._id} value={s._id}>{s.name}</MenuItem>)}
        </TextField>
      </Stack>

      <Paper variant="outlined" sx={{ borderRadius: 3, overflow: 'hidden' }}>
        <Tabs value={tab} onChange={(_,v)=>setTab(v)} aria-label="tabs">
          <Tab value="staff" label="Mitarbeiter" />
          <Tab value="services" label="Services" />
        </Tabs>
        <Divider />
        <Box sx={{ p: 2 }}>
          {/* 2-column layout */}
          <Stack direction={{ xs:'column', md:'row' }} spacing={2}>
            {/* LEFT: Global katalog */}
            <Paper variant="outlined" sx={{ flex: 1, p: 2, borderRadius: 2 }}>
              <Stack direction="row" spacing={1} alignItems="center" sx={{ mb: 1 }}>
                <Typography variant="h6" fontWeight={700}>Globaler Katalog</Typography>
                <Box sx={{ flex: 1 }} />
                <TextField
                  size="small" placeholder="Suchen…"
                  value={queryLeft} onChange={(e)=> setQueryLeft(e.target.value)}
                />
              </Stack>
              <Divider sx={{ mb: 1 }} />
              <List dense>
                {leftList.map(item => {
                  const id = (item as any)._id
                  const rightIds = (tab === 'staff' ? assignedStaff : assignedServices).map(x => (x as any)._id)
                  const already = rightIds.includes(id)
                  return (
                    <ListItem
                      key={id}
                      secondaryAction={
                        <Tooltip title={already ? 'Bereits zugeordnet' : 'Zuordnen'}>
                          <span>
                            <IconButton edge="end" disabled={already} onClick={()=> assign(id)}>
                              <AddIcon />
                            </IconButton>
                          </span>
                        </Tooltip>
                      }
                    >
                      <ListItemText
                        primary={tab === 'staff'
                          ? ((item as GlobalStaff).name || (item as GlobalStaff).email)
                          : (item as GlobalService).title}
                        secondary={tab === 'staff'
                          ? (item as GlobalStaff).email
                          : `Basis: ${(item as GlobalService).price}€ • ${(item as GlobalService).duration} Min`}
                      />
                    </ListItem>
                  )
                })}
              </List>
            </Paper>

            {/* RIGHT: Zuordnungen für Salon */}
            <Paper variant="outlined" sx={{ flex: 1, p: 2, borderRadius: 2 }}>
              <Stack direction="row" spacing={1} alignItems="center" sx={{ mb: 1 }}>
                <Typography variant="h6" fontWeight={700}>
                  Zugeordnet zu: {salons.find(s => s._id === salonId)?.name || ''}
                </Typography>
                <Box sx={{ flex: 1 }} />
                <TextField
                  size="small" placeholder="Suchen…"
                  value={queryRight} onChange={(e)=> setQueryRight(e.target.value)}
                />
              </Stack>
              <Divider sx={{ mb: 1 }} />
              <List dense>
                {rightList.map(item => {
                  const id = (item as any)._id
                  return (
                    <ListItem
                      key={id}
                      secondaryAction={
                        <Stack direction="row" spacing={0.5} alignItems="center">
                          {tab === 'services' && (
                            <Tooltip title="Overrides (Preis/Dauer) setzen">
                              <IconButton onClick={() => {
                                setOverrideSvc(item as GlobalService)
                                const cur = assignedServices.find(s => s._id === id)
                                setOverridePrice(cur?.price ?? '')
                                setOverrideDuration(cur?.duration ?? '')
                                setOverrideOpen(true)
                              }}>
                                <TuneIcon />
                              </IconButton>
                            </Tooltip>
                          )}
                          <Tooltip title="Zuordnung entfernen">
                            <IconButton color="error" onClick={()=> unassign(id)}>
                              <DeleteIcon />
                            </IconButton>
                          </Tooltip>
                        </Stack>
                      }
                    >
                      <ListItemText
                        primary={tab === 'staff'
                          ? ((item as GlobalStaff).name || (item as GlobalStaff).email)
                          : (item as GlobalService).title}
                        secondary={tab === 'staff'
                          ? (item as GlobalStaff).email
                          : (() => {
                              const s = item as any
                              return (
                                <>
                                  <Chip size="small" label={`Preis: ${s.price}€`} sx={{ mr: .5 }} />
                                  <Chip size="small" label={`Dauer: ${s.duration} Min`} />
                                </>
                              ) as any
                            })()}
                      />
                    </ListItem>
                  )
                })}
              </List>
            </Paper>
          </Stack>
        </Box>
      </Paper>

      {/* Override Dialog */}
      <Dialog open={overrideOpen} onClose={() => setOverrideOpen(false)} fullWidth maxWidth="sm">
        <DialogTitle>Overrides für Service {overrideSvc?.title}</DialogTitle>
        <DialogContent sx={{ display:'flex', flexDirection:'column', gap:2, pt:2 }}>
          <TextField
            type="number" label="Preis (optional Override)"
            value={overridePrice} onChange={(e)=> setOverridePrice(e.target.value === '' ? '' : Number(e.target.value))}
            InputProps={{ inputProps: { min: 0 } }}
          />
          <TextField
            type="number" label="Dauer in Minuten (optional Override)"
            value={overrideDuration} onChange={(e)=> setOverrideDuration(e.target.value === '' ? '' : Number(e.target.value))}
            InputProps={{ inputProps: { min: 1 } }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={()=> setOverrideOpen(false)}>Abbrechen</Button>
          <Button variant="contained" onClick={applyServiceOverride}>Speichern</Button>
        </DialogActions>
      </Dialog>
    </Container>
  )
}
